.preview-panel{
  position:relative;
  display:none;
  width:100%;
  height:100%;
  background:#3f3f3f;

  &.show{
    display:flex;
    flex-direction:column;
  }

  .btn{
    display:flex;
    margin:10px;
    font-size:14px;

    div{
      width:100px;
      height:30px;
      background:#005df7;
      border-radius:3px;
      line-height:30px;
      text-align:center;
      color:#FFF;

      &:hover{
        background:#336dff;
      }
    }
  }

  .type{
    display:flex;
    margin:0 10px 10px;

    label{
      margin-right:10px;
    }

    input{
      margin-right:2px;
    }
  }

  .container{
    flex:1;
    display:flex;
    padding:0 10px 10px;

    .menu{
      margin:0 10px 0 0;
      width:100px;
      background:#313131;
    }

    .stage{
      flex:1;
      position:relative;
      background:#313131;

      canvas{
        position:absolute;
        left:50%;
        top:50%;
        box-sizing:border-box;
        max-width:100%;
        max-height:100%;
        border:1px solid #444;
        background:#000;
        transform:translate3d(-50%, -50%, 0);
      }
    }

    .side{
      margin:0 0 0 10px;
      padding:10px;
      width:140px;
      background:#313131;

      label{
        display:block;
        margin-bottom:5px;

        input[type=number]{
          margin-left:5px;
          padding:0 5px;
          width:24px;
          height:24px;
          background:#000;
          border:none;
          border-radius:3px;
          color:#FFF;
          text-align:center;
        }

        input[type=checkbox]{
          margin-right:2px;
        }
      }

      .export{
        width:70px;
        height:30px;
        background:#005df7;
        border-radius:3px;
        line-height:30px;
        text-align:center;
        color:#FFF;

        &:hover{
          background:#336dff;
        }

        &:active{
          color:#E30;
        }
      }
    }
  }
}
